.Container{
  display: flex;

}

.Left{
  position: relative;
  background: #3848a1;
  height: 100%;
 
}

.Left h1{
  text-align: center;
  background: #3848a1 ;
  color: aliceblue;
  padding: .5rem;
  margin-top: auto;
  
}

.Left h3{
  text-align: center;
  color: #3848a1; 
  padding: .5rem;
  margin-top: 9%;
  text-decoration-style: double;
  background-color: white;
}

.Left > .MuiPickersStaticWrapper-staticWrapperRoot{
  transform: scale(0.9);
  border: 1px solid;
  padding: 10px;
  box-shadow: 5px 10px #888888;
}

/* ///Right component// */

.Right_Page{
  
  justify-content: center;
  align-items: center;
  background: white ;
  height: 100vh;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.Right_Page .heading{
  text-align: center;
  background: #3848a1 ;
  color: aliceblue;
  padding: .5rem;
  margin: 0;
  
}

.Right_Page .Lists{
  text-align: center;
  padding: 3rem;
  color: #3848a1;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  
}

.Right_Page .Lists h2{
  background-color: #3848a1;
  padding: 1rem;
  color: aliceblue;
}

.Right_Page .Lists h1{
  color: purple;
}

.Right_Page .Lists h3{
  color: #3848a1;
}

.Left .MuiSvgIcon-root{
  color: blanchedalmond;
  text-align: center;
  transform: scale(2);
  align-items: center;
}

.MuiTypography-body1 {
  font-size: 1rem;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0.00938em;
  color: white;
}


.ADDTASK h1{
  text-align: center;
  
  color: #3848a1;
}
.MuiTypography-subtitle1{
  text-align: center;
  background: #3848a1;
  color: aliceblue;
  padding: 2rem;
  
  font-weight: bold;
}
.formm{
  text-align: center;
}

.formm .addBtn{
  padding: 3rem;
  width: auto;
}

.fullform{
  margin: 0;
  width: fit-content;
}

@media only screen and (max-width: 600px){
  .Right_Page{
    width: auto;
  }
  .Left{
    width: 360px;
  }
  .Left > .MuiPickersStaticWrapper-staticWrapperRoot{
    transform: scale(.9);
    padding: auto;
    
  }
  
  
  
}
